$textColor: white;
$bgColor: black;

$clr_softBlack: #222;
$clr_softWhite: #eee;

* {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
  width: 100%;
  list-style: none;
  text-decoration: none;
}

body {
  font-family: Tahoma, sans-serif;
  margin: 2rem;
}

.wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(12rem, 16rem));
  gap: 2rem;
  justify-content: center;
}

.card{
  overflow: hidden;
  box-shadow: 0 2px 20px gray;
  border-radius: 5%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  transition: transform 200ms ease-in;
  
  .card-image{
    height: 12rem;
	width: 100%;
	object-fit: cover;
  }
  
  .card-title{
    padding: 1rem;
  }
  
  .card-description{
    padding: 0 1.5rem; 
  }
  .card-button{
    padding: 1rem;
	width: 80%;
	font-family: inherit;
	font-weight: bold;
	font-size: 1rem;
	margin: 1rem auto;
	border: 2px solid black;
	background: transparent;
	color: black;
	border-radius: 10%;
	transition: background 200ms ease-in, color 200ms ease-in;
  }
  
  &:hover{
    transform: scale(1.02);
  }
  
  &:hover .card-button {
    background: black;
	color: white;
  }
}

